<template>
    <div>
        <div class="p-4 bg-white">
            <button type="button"  class="btn btn-sm " @click="gantiMode('create')" :class="`${mode=='create'?'btn-success':'btn-dark '}`">Create <span class="typcn typcn-pen"></span></button>
            <button type="button"  class="btn btn-sm " @click="gantiMode('edit')" :class="`${mode=='edit'?'btn-warning':'btn-dark '}`">Edit <span class="typcn typcn-edit"></span></button>
            <button type="button"  class="btn btn-sm " @click="gantiMode('delete')" :class="`${mode=='delete'?'btn-danger':'btn-dark '}`">DELETE <span class="typcn typcn-cancel"></span></button>
            <button type="button"  class="btn btn-sm " @click="gantiMode('free')" :class="`${mode=='free'?'btn-primary':'btn-dark '}`">FREE MOVE <span class="typcn typcn-map"></span></button>
            <button type="button" @click="getPoly" class="btn btn-sm btn-style9   waves-effect">get poly</button>
        </div>
         <div id="mapid" style="height:100vh;">
             
         </div>
    </div>
</template>
<script>
// import FreeDraw from 'leaflet-freedraw';
import FreeDraw, { CREATE, EDIT,DELETE,NONE,ALL } from 'leaflet-freedraw';
const freeDraw = new FreeDraw();

export default{
    data(){
        return{
            mode:'free'
        }
    },
    methods: {
        gantiMode(item){
            this.mode=item;
            freeDraw.mode(this.mode=='create'?CREATE:this.mode=='edit'?EDIT:this.mode=='delete'?DELETE:NONE);
            this.$forceUpdate();
        },
        getPoly(){
            console.log(freeDraw)
        }
    },
    mounted() {
        setTimeout(() => {
            const map = new L.Map('mapid');
            // const freeDraw = new FreeDraw();

            map.addLayer(freeDraw);
            map.setView([51.505, -0.09], 13);
            // L.tileLayer('https://api.mapbox.com/styles/v1/{id}/tiles/{z}/{x}/{y}?access_token=pk.eyJ1IjoicnVkeXNrYSIsImEiOiJja3B1cGY0OGwxOTlxMm5xdnJ6dTU3dXZiIn0.vqqfQlT3-d3KF9KNzemANg', {
            L.tileLayer('https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png', {
                attribution: 'Map data &copy; <a href="https://www.openstreetmap.org/copyright">OpenStreetMap</a> contributors, Imagery Â© <a href="https://www.mapbox.com/">Mapbox</a>',
                maxZoom: 18,
                id: 'mapbox/streets-v11',
                tileSize: 512,
                zoomOffset: -1,
                accessToken: 'pk.eyJ1IjoicnVkeXNrYSIsImEiOiJja3B1cGY0OGwxOTlxMm5xdnJ6dTU3dXZiIn0.vqqfQlT3-d3KF9KNzemANg'
            }).addTo(map);
            
            // freeDraw.mode('ALL');
             freeDraw.on('markers', event => {
                 console.log(event.eventType)
                // Listen for any markers added, removed or edited, and then output the lat lng boundaries.
                console.log('LatLngs:', event.latLngs, 'Polygons:', freeDraw.size());

            });
             document.addEventListener('keydown', event => {
                // Cancel the current FreeDraw action when the escape key is pressed.
                event.key === 'Escape' && freeDraw.cancel();
            });
            
        }, 2000);
    },
     metaInfo: {
        title: 'Cuaca API',
        meta: [
         { charset: 'utf-8' },
         { name: 'viewport', content: 'width=device-width, initial-scale=1' }
        ],
        link: [
         { rel: 'stylesheet', href: 'https://unpkg.com/leaflet@1.7.1/dist/leaflet.css', integrity:'sha512-xodZBNTC5n17Xt2atTPuE1HxjVMSvLVW9ocqUKLsCC5CXdbqCmblAshOMAS6/keqq/sMZMZ19scR4PsZChSR7A==',crossorigin:'' },
        ],
        script: [
            { src: 'https://unpkg.com/leaflet@1.7.1/dist/leaflet.js', integrity: "sha512-XQoYMqMTK8LvdxXYG3nZ448hOEQiglfqkJs1NOQV44cWnUrBc8PkAOcXy20w0vlaXaVUearIOBhiXZ5V3ynxwA==", crossorigin: ''}
        ],
    },
}
</script>